'use strict';

/**
* Allocate a buffer containing a provided string.
*
* @module @stdlib/buffer-from-string
*
* @example
* import string2buffer from '@stdlib/buffer-from-string';
*
* var buf = string2buffer( 'beep boop' );
* // returns <Buffer>
*/

// MODULES //

import hasFrom from './has_from.js';
import main from './main.js';
import polyfill from './polyfill.js';


// MAIN //

var string2buffer;
if ( hasFrom ) {
	string2buffer = main;
} else {
	string2buffer = polyfill;
}


// EXPORTS //

export default string2buffer;
